<template>
  <div class="Live">
    Live
    <div v-for="(item,key) in list" :key="key" @click="handleClick(item)">{{item.label}}</div>
    <MarkdownPreview v-if="markdownTxt" :initialValue="markdownTxt" />
  </div>
</template>
<script>
const compContext = require.context("./", true, /\.md$/);
// console.log(compContext)
const list = compContext.keys().map(url => {
console.log(url)
  return {
    label: url.replace(/.md/, "").replace(/.\//, "").replace(/\//g, "-"),
    url
  };
});
import Vue from "vue";
import axios from "axios";
import Markdown from "vue-meditor";
import { MarkdownPreview } from "vue-meditor";
export default {
  name: "Live",
  components: {
    Markdown,
    MarkdownPreview
  },
  computed: {},
  data() {
    return {
      list,
      activeComponent: null,
      markdownTxt:null
    };
  },
  mounted() {},
  methods: {
    handleClick(item) {
      this.markdownTxt = require(`${item.url}`)
    }
  }
};
</script>
