<template>
  <div class="notes">
    <div class="label">
      笔记
    </div>
    <div v-for="(item,key) in list" :key="key" @click="activeComponent = item">{{item}}</div>
    <component :is="activeComponent" v-if="activeComponent" class="github readme demo-component" />
  </div>
</template>
<script>
let list = [];
const components = {};
const compContext = require.context("./", true, /\.vue$/);

compContext.keys().forEach(url => {
  if (url !== './index.vue'){
    let component = compContext(url).default;
    let componentName = component["name"]
    list.push(componentName);
    components[componentName] = component;
  }
});
import Vue from "vue";
import axios from "axios";
export default {
  name: "Notes",
  components,
  computed: {},
  data() {
    return {
      list,
      activeComponent: null,
    };
  },
  mounted() {},
  methods: {

  }
};
</script>
